import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subject } from 'rxjs';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { CategoryManagerService } from '../category.service';
import { takeUntil } from 'rxjs/operators';
import { AuthUserService } from 'src/app/core/auth-user.service';

@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html'
})
export class AddCategoryComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  categoryForm: FormGroup;
  logginIn = false;
  error: string;

  @Input()
  category: any;

  @Input()
  operation: 'CREATE' | 'UPDATE' = 'CREATE';
  constructor(
    // private router: Router,
     private formBuilder: FormBuilder,
     private activeModal: NgbActiveModal,
     private categoryManager: CategoryManagerService,
    // private authManagerService: AuthManagerService,
    // private route: ActivatedRoute,
    private authUserService: AuthUserService

  ) {
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  ngOnInit(): void {
    this.categoryForm = this.formBuilder.group({
      category_id : this.category.category_id,
      name: [this.category.name, Validators.required],
      status: [this.category.status, Validators.required],
      owner_id: [this.authUserService.user['vendor_id'], Validators.required]
    });
  }

  submit(): void {
    if (this.operation == 'CREATE') {
      this.categoryManager.addCategory(this.categoryForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    } else {
      this.categoryManager.updateCategory(this.categoryForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    }
  }

  cancel(): void {
    this.activeModal.close(false);
  }

}
