import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { TableState } from '../core/table-state';
import { CategoryService } from '../core/category.service';
import { AuthUserService } from '../core/auth-user.service';

@Injectable()
export class CategoryManagerService {

  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];

  private _categoryList$ = new BehaviorSubject<any[]>(null);
  get categoryList$(): Observable<any[]> {
    return this._categoryList$.asObservable();
  }

  constructor(private categoryService: CategoryService, private authUserService: AuthUserService) { }

  addCategory(category: any): Observable<any> {
    // const category = {
    //   name: category['name'],
    //   email: category['email'],
    //   password: category['password'],
    //   address: category['address'],
    //   city: category['city'],
    //   mobile: category['mobile'],
    //   status: category['status']
    // };
    return this.categoryService.addCategory(category);
  }

  updateCategory(category: any): Observable<any> {
    // const category = {
    //   _id: category['_id'],
    //   name: category['name'],
    //   email: category['email'],
    //   address: category['address'],
    //   city: category['city'],
    //   mobile: category['mobile'],
    //   status: category['status']
    // };
    return this.categoryService.updateCategory(category);
  }

  deleteCategory(id: string): Observable<any> {
    return this.categoryService.deleteCategory(id);
  }

  getCategory(id: string): Observable<any> {
    return this.categoryService.getCategory(id);
  }


  getCategoryList(): void {

    this.categoryService.getCategoryList(this.authUserService.user['vendor_id'])
      .subscribe((data: any[]) => {
        console.log('Got Categorys for you: ', data);
        this._categoryList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._categoryList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm));
      this._categoryList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}
