import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, from } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CategoryManagerService } from '../category.service';
import { filter, takeUntil } from 'rxjs/operators';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { AddCategoryComponent } from '../add-category/add-category.component';

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html'
})
export class CategoryListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  constructor(public categoryManagerService: CategoryManagerService, private ModalService: NgbModal) {
  }

  ngOnInit(): void {
    this.categoryManagerService.getCategoryList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  addCategoryModal(): void {
    const modalRef = this.ModalService.open(AddCategoryComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    const category = {
      category_id: '',
      name: '',
      status: 'Active'
    }
    modalRef.componentInstance.category = category;
    modalRef.componentInstance.operation = 'CREATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.categoryManagerService.getCategoryList());
  }

  deleteCategory(category: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.category = category;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.categoryManagerService.getCategoryList());
  }

  updateCategory(category: any): void {
    const modalRef = this.ModalService.open(AddCategoryComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.category = category;
    modalRef.componentInstance.operation = 'UPDATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.categoryManagerService.getCategoryList());
  }
}
